*{
  margin:0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Lobster', cursive;
}
.container{
  box-shadow: 0px 1px 10px #00ffff63;
  max-width: 800px;
  margin: 0 auto;
}

header{
   
    background-repeat: no-repeat;
    display:flex;
    padding: 16px;
}
.foto{
    /* modificar el tamano de la foto */
    flex-basis: 30%;
    text-align: center;
}
.foto img{
    max-width: 160px; 
    border:2px solid #00c8d2;  
}
.texto{
    flex-basis: 165%;
    color: #00c8d2;
    font-size: 18px;
}
.Texto1 h3{
    font-weight: 200;
    padding-top: 10px;
    max-width: 480px;
}
.nombre{
    color: aqua;
}
.Texto1{
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    padding-top: 10px;
}
